@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light+Two&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #333136;
  min-height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.layout {
  margin: 10vh 15% 80vh 15%;
}

.video {
  position:fixed;
  background-position:center;
  background-size:cover;
  right:0;
  bottom:0;
  width:100vw;
  z-index:-10;
  -webkit-filter:blur(3px);
          filter:blur(3px);
  background-color:#282c34;
  background-image:-webkit-gradient(linear, left top, left bottom, from(#202020), color-stop(#282c34), to(#202020));
  background-image:-webkit-linear-gradient(#202020, #282c34, #202020);
  background-image:linear-gradient(#202020, #282c34, #202020);
}

.name {
  font-size:1.2em;
}

.quotes {
  font-family: 'Shadows Into Light Two', cursive;
  font-size:1.1em;
}

@media only screen and (max-width: 1024px) {
  .layout {
    margin-top: 5vh;
    margin-bottom:100vh;
  }
  .video {
    height:100vh;
    width:100%;
  }
}
